.process-wrapper {
  background-color: #fff;
  padding: 0 20px;
}

.process-box {
  max-width: 1472px;
  background-color: #fcfcfd;
  padding-top: 160px;
}

.process {
  @extend %to-align;
  display: flex;
  //flex-wrap: wrap;
}

.part-top {
  display: flex;
  flex-wrap: wrap;
}

.part-bottom {
  display: flex;
  flex-wrap: wrap;
}

.process__text {
  width: 362px;
  padding-top: 64px;
  padding-bottom: 291px;
  font-style: normal;
  font-weight: normal;
  font-size: 16px;
  line-height: 150%;
  color: #334563;
}

.process-card {
  position: relative;
  max-width: 240px;
  height: 256px;
  background-color: #fff;
  text-align: center;
  padding: 48px 32px;
  margin: 58px;
  z-index: 1;
  box-shadow: 16px 16px 16px rgba(0, 0, 0, 0.02);

  &__title {
    font-style: normal;
    font-weight: 900;
    font-size: 40px;
    line-height: 150%;
    text-align: center;
    letter-spacing: 2px;
    text-transform: uppercase;
  }

  &__title::before {
    display: block;
    position: absolute;
    content: "";
    width: 48px;
    height: 48px;
    border-radius: 50%;
    left: 50%;
    transform: translate(-50%, -30%);
    z-index: -1;
  }

  &__text {
    font-weight: normal;
    font-size: 12.6px;
    line-height: 150%;
    color: #253757;
  }
}

.one::before {
  background-color: #f0fbfe;
}

.one {
  color: #65d3f0;
}

.two::before {
  background-color: #fffaea;
}

.two {
  color: #fdc341;
}

.three::before {
  background-color: #e8fbef;
}

.three {
  color: #00d965;
}

.four::before {
  background-color: #fef0f0;
}

.four {
  color: #f66066;
}
